.section-slider{
	overflow: hidden;

	@media(max-width: $screen-md-max){
		padding-top: 50px;
	}

	/* slider */
	.portfolio-slider-wrapper{
		position: relative;
		margin: {
			left: auto;
			right: auto;
		}

		@media(max-width: $screen-md-max){
			width: 70%;
		}
		@media(min-width: $screen-md){
			width: 80%;
		}
		
		&::before,
		&::after{
			content: '';
			width: 200%;
			height: 100%;

			display: block;
			position: absolute;
			top: 0;
			z-index: 2;
			
			@media(max-width: $screen-md-max){
				background-color: $color-white;
			}
			@media(min-width: $screen-md){
				background-color: rgba(0,0,0, .5);
			}
		}
		&::before{
			right: 100%;
		}
		&::after{
			left: 100%;
		}
	}
	.portfolio-slider{
		@media(max-width: $screen-md-max){
			height: 300px;
		}
		@media(min-width: $screen-md){
			height: calc(100vh - 190px);
		}

		img{
			display: block;
			max-width: 100%;
			height: auto;
		}

		.slick-list{
			overflow: visible;
		}

		.slick-slide{
			display: flex;
			align-items: center;
			flex-direction: column;

			outline: 0;	

			background:{
				size: cover;
				position: 50%;
				repeat: no-repeat;
			}
			color: $color-white;
			text-align: center;

			@media(max-width: $screen-sm-max){
				justify-content:  center;
			}
			@media(min-width: $screen-sm){
				justify-content:  flex-end;
			}

			@media(max-width: $screen-md-max){
				height: 300px;
			}
			@media(min-width: $screen-md){
				height: calc(100vh - 190px);
			}
			
			&__title{
				@extend .heading;

				@media(max-width: $screen-sm-max){
					margin-bottom: 0;

					font-size: 35px;
				}
			}
			&__text{
				@extend .subheading;
				
				padding: {
					left: 5px;
					right: 5px;
				}

				@media(max-width: $screen-sm-max){
					margin: 0;

					font-size: 16px;
				}
				@media(min-width: $screen-sm){
					margin: {
						top: 0;
						bottom: 10px;
					}
				}
			}
			.brush-font{
				margin-bottom: 20px;

				color: $color-primary;
				@media(max-width: $screen-sm-max){
					display: none;
				}
			}
		}

		.ps1{
			background-image: url('./images/nivea-slide-1.jpg');
		}
		.ps2{
			background-image: url('./images/anson-slide-1.jpg');
		}
		.ps3{
			background-image: url('./images/ford-slide-1.jpg');
		}
		.ps4{
			background-image: url('./images/tui-slide-1.jpg');
		}
		.ps5{
			background-image: url('./images/soliver-slide-1.jpg');
		}
		.ps6{
			background-image: url('./images/vodafone-slide-1.jpg');
		}
		.ps7{
			background-image: url('./images/levis-slide-1.jpg');
		}
		.ps8{
			background-image: url('./images/chemie-slide-1.jpg');
		}
	}

	/* slider arrows */
	.slick-prev,
	.slick-next{
		padding: 0;
		position: absolute;
		z-index: 10;
		outline: 0;
		background: none;
		text-indent: -9999px;

		transition: $transition;
		
		@media(max-width: $screen-md-max){
			top: 50%;
			margin-top: -22px;
		}
		@media(min-width: $screen-md){
			top: 0;
		}
	}
	.slick-prev{
		width: 0;
		height: 0;
		border-style: solid;

		@media(max-width: $screen-md-max){
			left: -40px;
			border-width: 22.5px 30px 22.5px 0;
			border-color: transparent $color-primary transparent transparent;
		}
		@media(min-width: $screen-md){
			left: -60px;

			border-width: calc((100vh - 190px)/2 ) 60px calc((100vh - 190px)/2 ) 0;
			border-color: transparent $color-white transparent transparent;

			&:hover{
				border-color: transparent $color-primary transparent transparent;			
			}
		}
	}
	.slick-next{
		width: 0;
		height: 0;
		border-style: solid;

		@media(max-width: $screen-md-max){
			right: -40px;
			border-width: 22.5px 0 22.5px 30px;
			border-color: transparent transparent transparent $color-primary ;
		}
		@media(min-width: $screen-md){
			right: -60px;

			border-width: calc((100vh - 190px)/2 ) 0 calc((100vh - 190px)/2 ) 60px;
			border-color: transparent transparent transparent $color-white;

			&:hover{
				border-color: transparent transparent transparent $color-primary;			
			}
		}
	}

}
